@import "@moai/core/dist/theme/module.scss";

.container,
.backdrop,
.main {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.backdrop,
.main {
    position: absolute;
}

.container {
    position: fixed;
    z-index: 2;
}

.backdrop {
    opacity: 0.9;
}

.main {
    display: flex;
    align-items: center;
    justify-content: center;
}

.body {
    border-width: 1px;
    border-style: solid;
    padding: 16px;
    width: 480px;

    @include light {
        box-shadow: 0px 1px 4px var(--light-gray-2);
    }

    @include dark {
        box-shadow: 0px 1px 4px var(--dark-gray-0);
    }
}

.footer {
    display: flex;
    justify-content: flex-end;
}

.title {
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
}
