@import "@moai/core/dist/theme/module.scss";

.container {
    pointer-events: all;
}

.from,
.to {
    @include light {
        fill: var(--light-gray-0);
        stroke: var(--light-gray-4);
    }
    @include dark {
        fill: var(--dark-gray-1);
        stroke: var(--dark-gray-4);
    }
    stroke-width: 1px;
    transition: stroke 0.2s, stroke-width 0.2s;
}

.path {
    fill: none;
    stroke-width: 1px;
    stroke-linejoin: round;
    transition: stroke 0.2s, stroke-width 0.2s;
    @include light {
        stroke: var(--light-gray-4);
    }
    @include dark {
        stroke: var(--dark-gray-4);
    }
    // Markers must be defined inside CSS in order to have better priority
    // when exporting to images
    // See: https://github.com/tsayen/dom-to-image/issues/125#issuecomment-480301690
    marker-end: url("#edge-to");
    marker-start: url("#edge-from");
}

.dot {
    fill: var(--accent-2);
}

:global(.temp-selected),
.selected {
    .path,
    .from,
    .to {
        @include light {
            stroke: var(--accent-2);
        }
        @include dark {
            stroke: var(--accent-2);
        }
        stroke-width: 2px;
    }
}

.hitarea {
    stroke-width: 20px;
    stroke: transparent;
    fill: none;
    cursor: pointer;
}
