@import "@moai/core/dist/theme/module.scss";

.container {
    width: 104px;
}

.view {
    line-height: 24px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    white-space: nowrap;

    &:not(.weak) {
        transition: none;
    }
}

.edit {
    @include light {
        box-shadow: 0px 0px 3px var(--accent-2);
    }
    @include dark {
        box-shadow: 0px 0px 6px var(--accent-2);
    }
}

.empty {
    font-style: italic;
}
