.container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    outline: none;
    user-select: none;
    -webkit-user-drag: element;
}

.box {
    border: solid 1px var(--accent-2);
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.2s;
}

.boxBg {
    height: 100%;
    background-color: var(--accent-2);
    opacity: 0.2;
}
