@import "@moai/core/dist/theme/module.scss";

.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    pointer-events: none;
}

.svg {
    width: 100%;
    height: 100%;
}

.line {
    stroke: var(--accent-2);
    stroke-width: 2px;
}

.icon {
    position: absolute;
} 
