@import "@moai/core/dist/theme/module.scss";

.container {
    transition: outline 0.2s ease-out;
    outline: solid 8px transparent;
    outline-offset: -1px;
}

.container {
    border: solid 1px transparent;
    margin: -1px;

    @include light {
        box-shadow: 0px 1px 4px var(--light-gray-2);
    }

    @include dark {
        box-shadow: 0px 1px 4px var(--dark-gray-0);
    }
}

.container:focus {
    outline: none;
}

:global(.temp-selected) .container,
.container.selected {
    outline: solid 2px var(--accent-2);
}

.container.edit {
    /* Eliminate the border and first and last column */
    margin-left: -29px;
    margin-right: -29px;
}
