@import "@moai/core/dist/theme/module.scss";

.container {
    text-align: center;
    font-weight: 600;
    flex: 0 0 auto;
}

.view {
    line-height: 24px; // Mimic text box
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    // mimic text-box
    &:hover {
        @include light {
            background-color: var(--light-gray-2);
        }
        @include dark {
            background-color: var(--dark-gray-2);
        }
    }
}
