@import "@moai/core/dist/theme/module.scss";

.container {
    position: relative;
}

.grid {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    background-size: 16px 16px;
    background-position: 8px 8px;

    // transition: transform 1s ease-in;

    @include light {
        background-image: radial-gradient(
            circle,
            var(--light-gray-2) 1px,
            transparent 1px
        );
    }

    @include dark {
        background-image: radial-gradient(
            circle,
            var(--dark-gray-1) 1px,
            transparent 1px
        );
    }
}
