@keyframes slide-in {
    from {
        transform: translateX(-50%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.container {
    animation: slide-in ease-out 0.15s;
    cursor: grab;
    color: #48BB78;
}

.dragging {
    color: var(--accent-2);
}

.button {
    cursor: grab;
    display: flex;
    align-items: center;
}
