@import "@moai/core/dist/theme/module.scss";

// @TODO these should be applied via ButtonStyle and ButtonSize
.container {
    // vertical: 7 + 1 + 24 + 1 + 6 = 37
    // left: 62 + 16 = 78
    padding: 7px 0 6px 62px;

    --light-gray-4: #adadad;
    --light-gray-5: #575757;
    @include light {
        background: linear-gradient(180deg, #eaeaea 0%, #cccccc 100%);
        box-shadow: 0px 0.5px 0px #bdbebe, 0px 1px 0px #a8a7a7,
            inset 0px 0.5px 0px rgba(255, 255, 255, 0.6);
        color: var(--light-gray-5);
    }

    --dark-gray-4: #919292;
    --dark-gray-5: #fcfdfd;
    @include dark {
        background: linear-gradient(180deg, #424343 0%, #363737 99.4%);
        box-shadow: 0px 0.5px 0px #272828, 0px 1px 0px #000000;
        color: var(--dark-gray-5);
    }

    button,
    select,
    input {
        height: 23px;
        outline: revert; // Safari and Firefox
        border-radius: 4px;
        @include light {
            background: linear-gradient(180deg, #fdfdfd 0%, #f3f3f3 100%);
            box-shadow: 0 0.5px 0 #c6c6c6;
            border-width: 1px;
            border-color: #c2c2c2;
            border-bottom-color: #acacac;
        }
        @include dark {
            background: linear-gradient(180deg, #707071 0%, #6a6a6a 100%);
            box-shadow: inset 0px 0.5px 0px rgba(255, 255, 255, 0.2);
            border-color: #3c3d3d;
        }
    }

    button:active {
        @include light {
            background: linear-gradient(180deg, #e3e3e3 0%, #d4d4d4 100%);
        }
        @include dark {
            background: linear-gradient(180deg, #8f8d8d 0%, #818080 100%);
        }
    }
}
